.counters {
  display: flex;
  @include r(750) {
    flex-direction: column; }
  .counter {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 80px 0 88px 0;
    color: #FFF;
    border-left: 1px solid #b5eae0;
    @include lch {
      border-right: 1px solid #b5eae0; }
    @include r(750) {
      border: none;
      padding: 20px 0;
      @include fch {
        padding-top: 0; }
      @include lch {
        border: none;
        padding-bottom: 0; } }
    &__value {
      font-size: 72px;
      font-weight: bold; }
    &__text {
      text-align: center;
      font-size: 14px;
      text-transform: uppercase; } } }
